import React, { Component } from 'react';
import { inject, observer } from 'mobx-react';
import Tablelayout from 'components/TableLayout';
import { Modal, Button, Empty } from 'antd';
import { isEmpty } from 'lodash';
import './index.less';

@inject('DeviceDefineStore')
@observer
class SearchBar extends Component {
	constructor(props) {
		super(props);
		this.store = this.props.DeviceDefineStore;
	}

	render() {
		const { logDetailList } = this.store;
		return (
			<Modal
				title=''
				visible={this.props.visible}
				onCancel={this.props.onCancel}
        width={'80%'}
				footer={
					<Button onClick={this.props.onCancel} type='primary'>
						
					</Button>
				}
			>
				<div style={{ marginTop: '20px' }}>
					<div className='breaken-table'>
            {!isEmpty(logDetailList) ? <table border='1' style={{ width:'100%' }}>
							<tr>
								<th></th>
								<th></th>
								<th style={{ borderBottom: 'none', borderRight: 'none' }}>
									
								</th>
							</tr>
                {
                  logDetailList.length > 0 && logDetailList.map((item,index)=>{
                    return <tr key={index}>
                      <td className='breaken-content'>{item.content}</td>
                      <td className='breaken-result'>{item.result}</td>
                      <td
                        className='breaken-detail'
                      >
                        {
                          item.resultVOList ? item.resultVOList.map((per,idx)=>{
                            return <tr key={idx} >
                              <td>{per.content}</td>
                              <td
                                style={{
                                  width: 210,height: per.contentType !== 5 && 55,padding: 10
                                }}
                              >{
                                per.contentType === 6 ? <span
                                  style={{
                                    height: '100%',display: 'inline-block',width: '100%',
                                  }}
                                >
                                  <audio src={per.result} controls='controls'
                                    style={{
                                      width: '100%',height: '100%'
                                    }}
                                  >
                                    
                                  </audio>
                                </span> : per.contentType === 5 ? <span>
                                  <span
                                    style={{
                                      display: 'inline-block',
                                      width: '50px',
                                      height: '50px',
                                      border: '1px solid',
                                      verticalAlign:'middle'
                                    }}
                                  >
                                    <img src={per.result} width='100%' alt='' />
                                  </span>
                                  <span 
                                    style={{color:'#6236FF',verticalAlign:'middle',cursor:'pointer',marginLeft:'5px'}}
                                    onClick={()=>{window.open(`${window.location.host}/${per.result}`)}}></span>
                                </span> : per.result
                              }</td>
                            </tr>
                          }) : <tr>
                          </tr>
                        }
                      </td>
                    </tr>
                  })
                }
						  </table> : <Empty />
            }
					</div>
				</div>
			</Modal>
		);
	}
	componentDidMount() {
		this.props.DeviceDefineStore.getSelectType({});
	}
}
export default SearchBar;
